@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

:root {
  --primary-color: #434343;
  --secondary-color: #000000;
  --tertiary-color: #ffefba;
  --light-gray: #f5f5f5;
  --card-color: rgba(128, 128, 128, 0.097);
}

body {

  background-color: #dcdddd ;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.3s ease-in-out; 
}

.navbar {
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 10px -6px rgba(0, 0, 0, 0.2); 
  border-bottom: solid var(--primary-color) !important;
  background-color: #f8f9fa;
}

.nav-link {
  font-weight: 300 !important;
  color: var(--primary-color);
  font-size: 1.1em; 
  font-weight: bold; 
}

.nav-link:hover {
  color: var(--secondary-color);
}

.active {
  border-bottom: 2px solid var(--secondary-color);
}

.navbar-brand {
  font-weight: 700;
  font-family: "Playfair Display", serif;
  letter-spacing: 3px;
  color: var(--primary-color);
}

@media (min-width: 992px) {
  .navbar-brand-full {
    display: block;
  }

  .navbar-brand-small {
    display: none;
  }
}

@media (max-width: 992px) {
  .navbar-brand-full {
    display: none;
  }

  .navbar-brand-small {
    display: block;
  }
}
.footer {
  color: white;
  padding: 25px;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: var(--tertiary-color);
  border-bottom: 1px solid var(--tertiary-color);
}


.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--light-gray);
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
  color: var(--primary-color);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5); /* Add text shadow */
}

.pnf-heading {
  font-weight: normal;
  color: var(--secondary-color);
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
  transition: all 0.5s ease-in-out; /* Add transition */
}

.pnf-btn:hover {
  background-color: black;
  color: white;
  transform: scale(1.1); /* Add scale transform on hover */
}

.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to right, var(--primary-color), var(--secondary-color));
}

.register,
.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-image: linear-gradient(0deg, var(--light-gray), var(--card-color));
}

.register-heading,
.login-heading {
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--primary-color);
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5); /* Add text shadow */
}

.register form,
.login form {
  max-width: 500px;
  width: 90%;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.2), 0px 4px 6px -2px rgba(0, 0, 0, 0.05); /* Update box shadow */
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
}

.register form input,
.login form input {
  border: none;
  border-bottom: 1px solid var(--primary-color);
  padding: 10px 5px;
  width: 100%;
  margin-bottom: 20px;
  transition: all 0.5s ease-in-out; /* Add transition */
}

.register form input:focus,
.login form input:focus {
  outline: none;
  border-bottom: 2px solid var(--secondary-color); /* Add focus style */
}

.register form button,
.login form button {
  border: none;
  background-color: var(--primary-color);
  color: white;
  width: 100%;
  padding: 10px;
  cursor: pointer;
  transition: all 0.5s ease-in-out; /* Add transition */
}

.register form button:hover,
.login form button:hover {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  transform: scale(1.05); /* Add scale transform on hover */
}

.product-link {
  text-decoration: none !important;
  color: var(--primary-color) !important;
}

.banner-img {
  margin-top: 60px;
}

.home-page h1 {
  color: var(--secondary-color);
  font-family: "Playfair Display", serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5); /* Add text shadow */
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}

.home-page .card,
.category .card,
.similar-products .card {
  background-color: var(--card-color);
  width: 18rem;
  border-radius: 8px;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.2), 0px 4px 6px -2px rgba(0, 0, 0, 0.05); /* Add box shadow */
  transition: all 0.5s ease-in-out; /* Add transition */
}

.home-page .card:hover,
.category .card:hover,
.similar-products .card:hover {
  box-shadow: 0px 25px 50px -12px rgba(0, 0, 0, 0.25); /* Add hover effect */
  transform: scale(1.05); /* Add scale transform on hover */
}

.home-page .card-name-price,
.category .card-name-price,
.similar-products .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price,
.category .card-price,
.similar-products .card-price {
  color: green;
  font-weight: bold;
}

.home-page button,
.category button,
.similar-products button {
  border-radius: 5px; /* Add border-radius */
  width: 100%;
  border: none; /* Remove border */
  background-color: var(--primary-color); /* Add background color */
  color: white; /* Change text color to white */
  font-size: 14px;
  font-weight: bold;
  transition: all 0.5s ease-in-out; /* Add transition */
}

.home-page button:hover,
.category button:hover,
.similar-products button:hover {
  background-color: var(--secondary-color); /* Change background color on hover */
  transform: scale(1.05); /* Add scale transform on hover */
}

.home-page .card-text,
.category .card-text,
.similar-products .card-text {
  color: var(--primary-color);
}

.home-page .card img:hover,
.category .card img:hover,
.similar-products .card img:hover {
  transform: scale(1.1); /* Increase scale on hover */
}

.filters h4 {
  margin-top: 100px;
  color: var(--primary-color);
  border-bottom: 1px solid var(--primary-color); /* Change border color */
}

.filters button {
  background-color: var(--primary-color); /* Change background color */
  width: 100%;
  border: none;
  border-radius: 5px; /* Add border-radius */
  margin-top: 20px;
  color: white; /* Change text color to white */
  transition: all 0.5s ease-in-out; /* Add transition */
}

.filters button:hover {
  background-color: var(--secondary-color); /* Change background color on hover */
}



.loadmore {
  color: green;
  font-weight: bold;
  font-size: 20px !important;
  text-align: center; /* Center text */
  margin: auto; /* Center block horizontally */
  transition: all 0.5s ease-in-out; /* Add transition */
}

.loadmore:hover {
  color: darkgreen; /* Change color on hover */
}


.cart-page {
  margin-top: 64px;
}

.cart-page h1 {
  padding: 15px !important;
  font-family: "roboto", sans-serif;
  font-weight: normal;
  background-color: var(--card-color) !important; /* Change background color */
  color: var(--primary-color); /* Change text color */
}

.cart-page h1 > p {
  font-size: 20px;
  margin-top: 10px;
}

.cart-page .card {
  padding: 5px !important;
  height: 150px !important;
  margin-bottom: 5px !important;
  background-color: var(--card-color); /* Change background color */
  border-radius: 8px; /* Add border-radius */
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.2), 0px 4px 6px -2px rgba(0, 0, 0, 0.05); /* Add box shadow */
}

.cart-page .cart-remove-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart-page .cart-summary {
  text-align: center;
}


.product-details {
  margin-top: 100px;
}

.product-details-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: var(--card-color);
  border-radius: 8px;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.2), 0px 4px 6px -2px rgba(0, 0, 0, 0.05);
  padding: 20px;
}

.product-details-info h6 {
  margin-bottom: 20px;
  color: var(--primary-color);
}

.category {
  flex-wrap: wrap;
  margin-top: 80px;
}

.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
  background-image: linear-gradient(0deg, var(--light-gray), var(--card-color));
}

.form-container form {
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.2), 0px 4px 6px -2px rgba(0, 0, 0, 0.05);
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 10px;
  
  border: 1px solid #ced4da;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  color: var(--primary-color);
}

.form-container form input {
  border: none;
  border-bottom: 2px solid var(--primary-color);
  padding: 10px 5px;
  width: 100%;
  margin-bottom: 20px;
  transition: all 0.5s ease-in-out;
}

.form-container form input:focus {
  outline: none;
  border-bottom: 2px solid var(--secondary-color);
}

.form-container form button {
  border: none;
  border-radius: 5px;
  background-color: var(--primary-color);
  color: white;
  width: 250px;
  transition: all 0.5s ease-in-out;
}

.form-container form button:hover {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  transform: scale(1.05);
}


.forgot-btn {
  background-color: var(--primary-color);
  border: none;
  color: white;
  transition: all 0.5s ease-in-out;
}

.forgot-btn:hover {
  background-color: var(--secondary-color);
  color: white;
}

.username {
  font-size: 1.5em; /* Increase font size */
  color: rgb(105, 128, 255); /* Change text color */
  font-weight: bold; /* Make it bold */
  margin-right: 20px; /* Add some space between the name and the button */
}

.logout {
  color: white; /* Make the button text white */
  background-color: rgb(105, 128, 255) ; /* Change the background color to blue */
  /* Add some padding around the text */
  border-radius: 50px; /* Make the button corners rounded */
  transition: all 0.5s ease-in-out; /* Add transition for hover effect */
  text-decoration: none; /* Remove underline from the link */
  margin-left: 30px;
  margin-right: 30px;
}

.bg-light {
  background-color: #f8f9fa !important;
  color: #343a40; /* Change the color of the text */
  font-size: 2em; /* Increase the size of the text */
  font-weight: bold; /* Make the text bold */
}

.text-center h4 {
  color: #6c757d; /* Change the color of the text */
  font-size: 1.5em; /* Increase the size of the text */
  font-weight: bold; /* Make the text bold */
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border: 1px solid #dee2e6;
  padding: 2px;
  margin: 20px;
}

.card h3 {
  font-size: 1.2em;
  color: #6c757d;
  font-weight: bold;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.btn-danger {
  background-color: #dc3545;
  border-color: #dc3545;
  color: #fff;
}

.btn-danger:hover {
  background-color: #c82333;
  border-color: #bd2130;
  color: #fff;
  transform: scale(1.05); /* Increase size on hover */
}

.btn-primary {
  background-color: #0d6efd;
  border-color: #0d6efd;
  color: #fff;
}

.btn-primary:hover {
  background-color: #0b5ed7;
  border-color: #0a58ca;
  color: #fff;
  transform: scale(1.05); /* Increase size on hover */
}

.border {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border: 1px solid #dee2e6;
  padding: 20px;
  margin: 20px;
}

.card p {
  font-size: 1em;
  color: #6c757d;
}

.data-facts-container {
  max-width: 1400px;
  margin: auto;
  background: #f0f0f0;
  border-radius: 10px;
  padding: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.data-facts-container h2 {
  text-align: center;
  color: #333;
  font-size: 24px;
  margin-bottom: 20px;
}

.data-facts-container p {
  font-size: 18px;
  line-height: 1.6;
  color: #666;
  text-align: justify;
}

.center {
  text-align: center;
  margin-top: 20px;
}


